:root {
  --fuenteHeading: "PT Sans", sans-serif;
  --fuenteParrafos: "Open Sans", sans-serif;

  --colorNegro: #000000;
  --colorGris: #757575;
  --colorBlanco: #ffffff;
  --grisclaro: #dfe9f3;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
}
*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: var(--fuenteParrafos);
  font-size: 1.6rem;
  line-height: 2;
  background-image: linear-gradient(
    to top,
    var(--grisclaro) 0%,
    var(--colorBlanco) 100%
  );
}

a {
  text-decoration: none;
}

h1,
h2,
h3,
h4 {
  font-family: var(--fuenteHeading);
  line-height: 1.2;
}

h1 {
  font-size: 4.8rem;
}

img {
  max-width: 100%;
  border: none;
}

.contenedor {
  max-width: 125rem;
  width: 90%;
  margin: 0 auto;
}

.no-margin {
  margin: 0;
}

.none {
  list-style: none;
  padding: 0;
}

/* Navegacion */
.webp .img-inicio{
    background-image: url(../../build/img/fondo.webp);
} 
.no-webp .img-inicio {
  background-image: url(../../build/img/fondo.jpg);
}
.img-inicio {
  height: 50rem;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  background-attachment: fixed;
  margin-bottom: 2rem;

  text-align: center;
  width: 100%;
  color: var(--colorBlanco);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.navegacion__enlace {
  color: var(--colorGris);
  display: flex;
  align-items: center;
}

.navegacion__enlace:hover {
  color: var(--colorNegro);
}

.navegacion {
  display: flex;
  gap: 2rem;
  margin-bottom: 5rem;
  justify-content: center;
}
 @media (min-width: 768px) {
  .navegacion {
    justify-content: left;
  }
} 

.btn-cv {
  background-color: #0047ab;
  color: var(--colorBlanco);
  font-size: 1.3rem;
  text-decoration: none;
  padding: 0.5rem 1rem;
  border-radius: 0.3rem;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  width: 13rem;
}

.contenido-principal{
  text-align: center;
 }

@media (min-width: 768px) {
  .contenido-principal {
    text-align: left;
  }
}

.sobremi, .contenido-principal span {
  font-weight: bold;
}

.sombra {
  -webkit-box-shadow: 0px 5px 15px 0px rgba(112, 112, 112, 0.48);
  -moz-box-shadow: 0px 5px 15px 0px rgba(112, 112, 112, 0.48);
  box-shadow: 0px 5px 15px 0px rgba(112, 112, 112, 0.48);
  background-color: var(--colorBlanco);
  padding: 3rem;
  border-radius: 1rem;
  margin: -15rem auto 0 auto;
}

@media(min-width: 768px) {
  .lista {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}

@media(min-width: 1080px) {
  .lista {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}

.card-img {
  display: block;
  border-top-left-radius: 0.5rem;
  border-top-right-radius: 0.5rem;
}

.card-body {
  padding: 1.5rem;
  border: 1px solid #c8c8c8;
  margin: -1rem 0 3rem;
  border-radius: 0.5rem;
  box-shadow: 0 1px 2px 1px #00000026;
}

.card-body h4 {
  line-height: 1.25;
  margin-top: 1rem;
  font-size: 1.6rem;
}

.card-body p {
  color: #46474a;
  font-size: 1.4rem;
  line-height: 1.5;
}

.btn-card {
  background-color: var(--colorBlanco);
  color: var(--colorNegro);
  font-size: 1.3rem;
  font-weight: bold;
  text-decoration: none;
  padding: 0.3rem 1rem;
  margin-top: 2.5rem;
  display: inline-block;
  border-radius: 0.3rem;
  border-style: solid;
  border-width: 1px;
  border-color: var(--colorNegro);
  display: flex;
  flex-direction: row;
  gap: 0.3rem;
  align-items: center;
}

.btn-card:hover {
  background-color: var(--colorNegro);
  color: var(--colorBlanco);
}

.contenedor-btn {
  text-align: center;
}

@media (min-width: 400px) {
  .contenedor-btn {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 2rem;
  }
}


.footer {
  padding: 2.5rem 0;
  background-color: var(--colorNegro);
  margin-top: 4rem;
  color: white;
  text-align: center;
}

.navegacion-footer {
  margin-top: 2rem;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 3rem;
}

.texto-footer {
  font-size: 1.2rem;
  margin: 0.5rem;
}

.navegacion__footer svg {
  text-decoration: none;
  color: var(--colorGris);
}

.navegacion__footer svg:hover {
  text-decoration: none;
  color: var(--colorBlanco);
}